html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
.vux-1px,
.vux-1px-t,
.vux-1px-b,
.vux-1px-tb,
.vux-1px-l,
.vux-1px-r {
  position: relative;
}
.vux-1px:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  border: 1px solid #C7C7C7;
  color: #C7C7C7;
  height: 200%;
  -webkit-transform-origin: left top;
          transform-origin: left top;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.vux-1px-t:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-b:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-tb:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-tb:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.vux-1px-l:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-left: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleX(0.5);
          transform: scaleX(0.5);
}
.vux-1px-r:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 100% 0;
          transform-origin: 100% 0;
  -webkit-transform: scaleX(0.5);
          transform: scaleX(0.5);
}
.vux-close {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  color: #999;
  width: 24px;
  height: 24px;
}
.vux-close:before,
.vux-close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 11px;
  width: 24px;
  height: 1px;
  background-color: currentColor;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.vux-close:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
/* @import '../../assets/css/reset';*/
body {
  background: #fff;
  font-size: 15px;
}
/* 进度提示条 */
.progress {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 30px 30px;
  font-size: 0;
}
.progress .step {
  position: relative;
  width: 33.33%;
}
.progress .step.active .step-line {
  background: #F9CD3B;
}
.progress .step.active .step-circle {
  background: #F9CD3B;
}
.progress .step.active span {
  background: #F9CD3B;
}
.progress .step-line {
  height: 6px;
  background: #D8D8D8;
}
.progress .step-circle {
  width: 30px;
  height: 30px;
  margin: 0 auto;
  margin-top: -17px;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  background: #D8D8D8;
  color: #fff;
  font-size: 16px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.progress .step-circle span {
  display: block;
  width: 26px;
  height: 26px;
  line-height: 26px;
  text-align: center;
  background: #bbb;
  border-radius: 50%;
}
.progress .step-titel {
  margin-top: 8px;
  text-align: center;
  color: #909090;
  font-size: 15px;
}
.progress .step1-line {
  border-bottom-left-radius: 3px;
  border-top-left-radius: 3px;
}
.progress .step3-line {
  border-bottom-right-radius: 3px;
  border-top-right-radius: 3px;
}
/* 输入手机号 */
.input-phone {
  padding: 10px;
}
.input-phone .phone {
  width: 100%;
  height: 47px;
  background: #F7F7F7;
  font-size: 17px;
  border-radius: 3px;
}
.input-phone .code {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 10px;
  font-size: 17px;
  height: 47px;
  background: #F7F7F7;
  border-radius: 3px;
}
.input-phone .code .send-btn {
  width: 110px;
  text-align: center;
  /*padding: 0 8px;*/
  line-height: 26px;
  color: #E1B113;
}
.input-phone .code .already-send {
  color: #999;
}
/* 下一步 */
.next-step {
  width: 330px;
  margin: 30px auto;
  line-height: 46px;
  background: #F9CD3B;
  text-align: center;
  font-size: 18px;
  color: #323232;
  border-radius: 23px;
}
/* 切换密码显示与否 */
.toggle-pwd {
  padding: 0 23px;
  text-align: right;
}
.toggle-pwd .hide-img {
  width: 22px;
  height: 10px;
}
.toggle-pwd .show-img {
  width: 22px;
  height: 16px;
}
/* 修改密码成功 */
.modify-suc {
  padding-top: 133px;
  text-align: center;
}
.modify-suc .suc-icon {
  width: 59px;
  height: 59px;
}
.modify-suc .suc-tip {
  margin: 5px 0;
  font-size: 21px;
  color: #323232;
}
.modify-suc .go-login {
  font-size: 18px;
  color: #E1B113;
  margin: 5px 0;
}
